export default function currentTime() {
	try {
		let options = {
			timeZone: 'Europe/London',
			hour12: false,
			hour: 'numeric',
			minute: '2-digit'
		};

		const time = new Date().toLocaleTimeString('en-US', options);

		return time;
	}
	catch(e) {
		let options = {
			timeZone: 'UTC',
			hour12: false,
			hour: 'numeric',
			minute: '2-digit'
		};

		const time = new Date().toLocaleTimeString('en-US', options);

		return time;
	}
}
