import React, { Component } from 'react';

import './style.scss';

class Loading extends Component {
    constructor(props) {
        super(props);

        this.loadingElement = React.createRef();
    }

    componentDidUpdate() {
        if (this.props.loaded) {
            setTimeout(() => {
                this.loadingElement.current.remove();
            }, 1000);
        }
    }

    render() {
        return <div ref={this.loadingElement} className={`loading w-100 fixed bg-yellow top-0 bottom-0 left-0 right-0 z-max m-auto${(this.props.loaded ? ' o-0' : '')}`}>
            
        </div>
    }
}

export default Loading;
