import React, { Component } from "react";

import './style.scss';

import melanomaUk from '../../images/melanoma-logo.png';
import festivalSafe from '../../images/festival-safe-logo.png';

class Footer extends Component {
    constructor(props) {
        super(props);

        this.logo1 = React.createRef();
        this.logo2 = React.createRef();
    }

    componentDidUpdate() {
        if (this.props.loaded === true) {
            this.logo1.current.classList.add('animated')
            this.logo1.current.classList.add('fadeInUp')
            
            this.logo2.current.classList.add('animated')
            this.logo2.current.classList.add('fadeInUp')
        }
    }

    render() {
        return (
            <>
                <footer className="footer-container w-100 pa3 pb5 ph5-ns relative flex flex-wrap flex-nowrap-ns justify-between items-end bottom-0">
                    <div ref={this.logo1} className="order-1 w-50 w-third-ns z-3"><a href="https://www.melanomauk.org.uk/" target="_blank" rel="noopener noreferrer"><img className="db footer-img footer-img--melanoma mw4-l" src={melanomaUk} alt="Melanoma UK charity" /></a></div>
                    <div className="order-1-ns order-0 mh2 mt3 mt0-ns pb4 pb0-ns w-100 w-third-ns"><a href="https://www.instagram.com/facskincancer/" target="_blank" rel="noopener noreferrer"><p className="fac-thin tc ma0 f6 f5-ns">#FACskinCANCER</p></a></div>
                    <div ref={this.logo2} className="order-1 w-50 w-third-ns z-3"><a className="fr" href="https://www.festivalsafe.com/" target="_blank" rel="noopener noreferrer"><img className="db footer-img mw4-l" src={festivalSafe} alt="Festival safe" /></a></div>
                </footer>

                <div className="footer-zigzag w-100 absolute bottom-0 z-3"></div>
            </> 
        )
    }
}

export default Footer;