import React, { Component } from 'react';
import { distanceBetween, angleBetween, getMouse, handlePercentage } from '../../utils/mouse';
import BrushImage from '../../images/brush';
import CanvasBackground from '../../images/canvasBackground';
import './style.scss';

class WipeOverlay extends Component {
    constructor(props) {
        super(props);

        this.canvas = React.createRef();

        this.brush = new Image();
        this.brush.src = BrushImage;
        this.coveredArea = 0;
    }

    componentDidUpdate(props, next) {
        if (this.props.loaded !== true) {
            return;
        }

        this.ctx = this.canvas.current.getContext('2d');
        
        window.addEventListener('resize', (ev) => {
            this.drawSunCover(ev);
        });
        
        setTimeout(() => {
            this.coveredArea = 0;

            this.drawSunCover();
        }, 100);

        this.canvas.current.addEventListener('mousedown', this.handleMouseDown.bind(this), false);
        this.canvas.current.addEventListener('touchstart', this.handleMouseDown.bind(this), false);
        this.canvas.current.addEventListener('mousemove', this.handleMouseMove.bind(this), false);
        this.canvas.current.addEventListener('touchmove', this.handleMouseMove.bind(this), false);
        this.canvas.current.addEventListener('mouseup', this.handleMouseUp.bind(this), false);
        this.canvas.current.addEventListener('touchend', this.handleMouseUp.bind(this), false);
    }

    drawSunCover(ev) {
        this.canvas.current.width = 0;

        let windowHeight = window.innerHeight;

        if (document.documentElement.offsetHeight > windowHeight) {
            windowHeight = document.documentElement.offsetHeight;
        }

        this.canvas.current.width = window.innerWidth;
        this.canvas.current.height = windowHeight;


        let image = new Image();

        image.src = CanvasBackground;
        image.onload = () => {
            this.ctx.drawImage(image, 0, 0, window.innerWidth, windowHeight);
        }
    }

    handleMouseDown(e) {
        this.isDrawing = true;
        this.lastPoint = getMouse(e, this.canvas.current);
    }

    handleMouseMove(e) {
        if (this.isDrawing !== true) {
            return;
        }

        e.preventDefault();

        var currentPoint = getMouse(e, this.canvas.current),
            dist = distanceBetween(this.lastPoint, currentPoint),
            angle = angleBetween(this.lastPoint, currentPoint),
            x, y;

        for (var i = 0; i < dist; i++) {
            x = this.lastPoint.x + (Math.sin(angle) * i) - 25;
            y = this.lastPoint.y + (Math.cos(angle) * i) - 25;

            this.ctx.drawImage(this.brush, x, y, 50, 50);

            this.coveredArea++;
        }

        this.lastPoint = currentPoint;
    }

    handleMouseUp(e) {
        this.isDrawing = false;

        handlePercentage(this.coveredArea, this.canvas.current);
    }

    render() {
        return (
            <>
                <div className="canvas">
                    <div className="bg-yellow fixed top-0 bottom-0 left-0 right-0 z-2"></div>

                    <canvas ref={this.canvas} className="absolute top-0 left-0 z-5" id="js-canvas" width="0" height="0" style={{ 'transition': 'opacity 750ms ease' }}></canvas>

                    <div className="absolute right-0 left-0 top-0 bottom-0 pa3 flex z-3 disabled-element">
                        <div className="w-50-ns w-100 m-auto flex flex-column items-center">
                            <h1 className="fac-thin tracked-giga tc z-3 apply-text no-select">APPLY <span className="db">FOR ENTRY</span></h1>
    
                            <div className="hands-container w-100 h4 flex items-center z-1">
                                <h2 className="fac-regular f6 black tc w-100 ph4 mt5 no-select">Click and move your mouse to apply the cream for entry</h2>
                            </div>
                        </div>
                    </div>
                </div>
            </>
        )
    }
}

export default WipeOverlay;
