import React, { Component } from 'react';

import currentTime from '../../utils/time';

import './style.scss';

let msgs = [ 'Apply suncream now', "It's hot out there", 'Apply suncream every 2 hours', "Don't go out with nowt on" ];

class MainHighSun extends Component {
	getText() {
		const time = parseInt(currentTime());
		if (time >= 12 && time <= 13) {
			return 'Feed your skin this lunchtime';
		} else {
			return msgs[Math.floor(Math.random() * msgs.length)];
		}
	}

	render() {
		return (
			<div className="main-container-high">
				<div className="high-sun-container mt4 mt0-l relative br-100 center bg-yellow flex items-center glow-constant-big">
					<h1 className="absolute tc ph2 top-0 bottom-0 left-0 right-0 h3 h4-l pt1 pt0-l m-auto fac-thin f3 f2-l ttu tracked-mega tc lh-title flex items-center w-90">
						{this.getText()}
					</h1>
				</div>
			</div>
		);
	}
}

export default MainHighSun;
