export function distanceBetween(point1, point2) {
	return Math.sqrt(Math.pow(point2.x - point1.x, 2) + Math.pow(point2.y - point1.y, 2));
}

export function angleBetween(point1, point2) {
	return Math.atan2(point2.x - point1.x, point2.y - point1.y);
}

export function getFilledInPixels(stride, ctx) {
	if (!stride || stride < 1) {
		stride = 1;
	}

	var pixels = ctx.getImageData(0, 0, window.innerWidth, window.innerHeight),
		pdata = pixels.data,
		l = pdata.length,
		total = l / stride,
		count = 0;

	// Iterate over all pixels
	for (var i = (count = 0); i < l; i += stride) {
		if (parseInt(pdata[i]) !== 255) {
			count++;
		}
	}

	return Math.round(count / total * 100);
}

export function getMouse(e, canvas) {
	var mx, my;

	mx = e.pageX ? e.pageX : e.touches && e.touches.length > 0 ? e.touches[0].clientX : 0;
	my = e.pageY ? e.pageY : e.touches && e.touches.length > 0 ? e.touches[0].clientY : 0;

	return { x: mx, y: my };
}

export function handlePercentage(filledInPixels, canvas) {
	filledInPixels = filledInPixels || 0;

	if (filledInPixels > 600) {
		canvas.parentNode.style.opacity = 0;

		setTimeout(function() {
			if (canvas && canvas.parentNode.parentNode) {
				canvas.parentNode.parentNode.removeChild(canvas.parentNode);
			}

			if (document.querySelector('.main-container-high')) {
				const mainContainer = document.querySelector('.main-container-high');
				mainContainer.classList.add('animated');
				mainContainer.classList.add('fadeInUpSmall');
			}

			if (document.querySelector('.image-container')) {
				const mainContainer = document.querySelector('.image-container');
				mainContainer.classList.add('animated');
				mainContainer.classList.add('fadeInUpSmall');
			}
		}, 1000);

		setTimeout(function() {
			if (document.querySelector('.main-text-left')) {
				const mainContainer = document.querySelector('.main-text-left');
				mainContainer.classList.add('animated');
				mainContainer.classList.add('fadeInUpSmall');
			}
		}, 1700);

		setTimeout(function() {
			if (document.querySelector('.main-text-right')) {
				const mainContainer = document.querySelector('.main-text-right');
				mainContainer.classList.add('animated');
				mainContainer.classList.add('fadeInUpSmall');
			}
		}, 2100);
	}
}
