import React, { Component } from 'react';

import "./style.scss";

class Menu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false
        }
    }

    toggleMenu() {
        this.setState({
            isOpen: !this.state.isOpen
        })
    }
    render() {
        return (
            <>
                <div className="menu-open w-third" onClick={() => this.toggleMenu()}></div>
                
                <div className={`menu-side fixed vh-100 w-100 w-50-ns top-0 right-0 bg-gray flex flex-column justify-center${(this.state.isOpen) ? ` menu-display` : ''}`}>
                    <div className="menu-close" onClick={() => this.toggleMenu()}></div>
                    <div className="mh4 mh6-l w-80 w-60-l">
                    <p className="white fac-regular">Whether you were there when the Hacienda began or if it’s brand new to you, keep the memory alive by protecting yourself from sun-related skin cancer. 
                    <span className="db pt3">Click through to our partners to learn more</span>
                    </p>
                    </div>
                    <nav className="ml4 ml6-l w-80 w-60-l">
                        <ul className="list fac-bold f3 f2-l white ttu pa0 ma0">
                            <a className="link" href="https://www.melanomauk.org.uk/" target="_blank" rel="noopener noreferrer"><li className="menu-link mv2 white ">Melanoma UK</li></a>
                            <a className="link" href="https://www.festivalsafe.com/" target="_blank" rel="noopener noreferrer"><li className="menu-link mv2 white">Festival safe</li></a>
                            <a className="link" href="https://www.surveymonkey.com/r/T2FTW6M" target="_blank" rel="noopener noreferrer"><li className="menu-link mv2 white">Sun Safe Survey</li></a>
                        </ul>
                        <p className="white fac-regular db pt3">Always give your skin the once-over, because life doesn’t come around twice.</p>
                        <a className="link" href="https://www.instagram.com/facskincancer/" target="_blank" rel="noopener noreferrer"><p className="menu-link insta-link mt4 white fac-thin f5">#FACskinCANCER</p></a>
                    </nav>
                </div>
            </>
        )
    }
}

export default Menu;
