import "@babel/polyfill";
import 'whatwg-fetch';
import 'string-includes-polyfill';
import '../styles/tachyons.scss';
import '../utils/polyfill.js';

import React, { Component } from 'react';
import { Helmet } from 'react-helmet';
import { getUV } from '../utils/api';
import ReactGA from 'react-ga';

import OnImagesLoaded from 'react-on-images-loaded';
import Wrapper from './Wrapper/index';
import Header from './Header/index';
import WipeOverlay from './WipeOverlay/index';
import Footer from './Footer/index';
import Loading from './Loading/index';

import Main from './Main/index';
import MainHigh from './MainHigh/index';

class App extends Component {
	state = {
		loaded: false,
		uv: false
	};

	componentDidMount() {
		ReactGA.initialize('UA-140592709-1');
		ReactGA.pageview(window.location.pathname + window.location.search);

		getUV().then((uv) => {
			this.setState({
				uv: uv
			});
		});
	}

	runAfterImagesLoaded() {
		setTimeout(() => {
			this.setState({
				loaded: true
			});
		}, 50);
	}

	render() {
		return (
			<OnImagesLoaded
				onLoaded={this.runAfterImagesLoaded.bind(this)}
				onTimeout={this.runAfterImagesLoaded.bind(this)}
				timeout={7000}
			>
				<Helmet>
					<title>FACTOR 51</title>
					<script
						type="text/javascript"
						src="//fast.fonts.net/jsapi/489d04e9-0c75-45d6-8a27-023e6605649c.js"
					/>
				</Helmet>

				<Wrapper>
					<Header uvIndex={this.state.uv} />
					{this.state.uv >= 3 ? <MainHigh /> : <Main />}
					<WipeOverlay loaded={this.state.loaded} />
					<Footer loaded={this.state.loaded} />
				</Wrapper>

				<Loading loaded={this.state.loaded} />
			</OnImagesLoaded>
		);
	}
}

export default App;
