import React, { Component } from 'react';

import './style.scss';

import bottleImg from '../../images/fac-50-bottle.png';
import useProtection from '../../images/fac50-use-protection.png';

const images = [useProtection];

class Main extends Component {
	constructor(props) {
		super(props);
		this.state = {
			image: false
		};
	}
	componentDidMount() {
		this.setState({
			image: this.loadImg()
		});
	}

	loadImg() {
		return images[Math.floor(Math.random() * images.length)];
	}

	render() {
		return (
			<main className="main-container mt4 mt0-l w-90 center flex flex-column flex-row-ns items-center justify-center pa3-ns pv4-ns">
				<div className="main-text-left w-100 w-third-ns mb3 dn db-ns h4">
					<h2 className="main-bold-text fac-bold ma0">
						MANCHESTER, IT’S HOTTING UP OUT THERE. APPLY SUNCREAM AND PARTY WITH PROTECTION.
					</h2>
				</div>
				<div className="image-container w-100 w-third-ns mb3 mh4-l flex flex-column items-center">
					<img
						className={`w-100 mw4 main-image ${(this.state.image && this.state.image.toString().includes('bottle'))
							? 'bottle'
							: 'face'}`}
						src={this.state.image ? this.state.image : ''}
						alt="Use sun protection"
					/>
				</div>
				<div className="main-text-right w-100 w-third-ns mb3 dn db-ns h4">
					<h2 className="fac-regular f6 f4-ns ma0">
						According to research, over 86% of Melanoma Skin Cancer cases are preventable.
						<span className="db pt3">
							Apply suncream every 2 hours and help protect your skin from future damage.
						</span>
					</h2>
				</div>
			</main>
		);
	}
}

export default Main;
